import React, { useLayoutEffect, useState } from "react";
import {
  Button,
  Container,
  makeStyles,
  Snackbar,
  Typography,
  TextField,
  CircularProgress,
} from "@material-ui/core";
import AuthContainer from "../../../containers/authContainer";
import { useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import {
  postForgotLogin,
  setAnswerForgot,
} from "../../../redux/reducers/forgot";
import { Alert } from "@mui/material";
const Reset = ({ t }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { answerForgotAlert } = useSelector(
    ({ forgotReducer }) => forgotReducer
  );
  const [reload, setReload] = useState(false);
  const formik = useFormik({
    initialValues: {
      email: "",
    },
    onSubmit: (values) => {
      dispatch(postForgotLogin(values));
      setReload(true);
    },
  });
  const firstUpdate = React.useRef(true);
  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerForgotAlert.severity === 200) {
      setTimeout(() => {
        history.push("/accounts/login");
      }, 1000);
    }
    setReload(false);
    // eslint-disable-next-line
  }, [answerForgotAlert.severity]);

  const useStyles = makeStyles((theme) => ({
    formControl: {
      "& .MuiOutlinedInput-input": {
        background: "transparent",
        boxShadow: "none !important",
        color: "#fff",
      },
      "& .MuiInputLabel-formControl": {
        color: "#B8C0CC !important",
        fontWeight: 300,
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
        overflow: "hidden",
        maxWidth: "90%",
        fontSize: 16,
      },
      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: "#B8C0CC",
      },
    },
  }));
  const classes = useStyles();
  return (
    <section className="dark">
      <Snackbar
        open={answerForgotAlert.open}
        autoHideDuration={4000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => dispatch(setAnswerForgot({ ...alert, open: false }))}
      >
        <Alert
          onClose={() => dispatch(setAnswerForgot({ ...alert, open: false }))}
          severity={answerForgotAlert.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {answerForgotAlert.message && answerForgotAlert.message.messages}
        </Alert>
      </Snackbar>
      <Container maxWidth="lg">
        <AuthContainer>
          <Typography variant="h3" gutterBottom component="div">
            {t("ResetTitle")}
          </Typography>
          <Typography
            style={{ maxWidth: 325, margin: "0 auto", lineHeight: ".9" }}
            variant="h5"
            gutterBottom
            component="div"
          >
            {t("ResetDesc")}
          </Typography>
          <form onSubmit={formik.handleSubmit} autoComplete="off">
            <span className="label">E-mail</span>

            <TextField
              label={t("LabelEmail")}
              type="email"
              variant="outlined"
              className={classes.formControl}
              name="email"
              value={formik.values.email}
              onChange={formik.handleChange}
              required
            />
            <Button
              style={{ marginTop: 24, height: 56, width: "100%" }}
              color="primary"
              variant="contained"
              size="large"
              type="submit"
            >
              {reload ? <CircularProgress disableShrink /> : t("SendLink")}
            </Button>
          </form>
        </AuthContainer>
      </Container>
    </section>
  );
};

export default Reset;
